@font-face {
    font-family: 'Open Sans';
    src: url('/assets/fonts/opensans/opensans_light_macroman/OpenSans-Light-webfont.eot');
    src: url('/assets/fonts/opensans/opensans_light_macroman/OpenSans-Light-webfont.eot?#iefix') format('embedded-opentype'),
         url('/assets/fonts/opensans/opensans_light_macroman/OpenSans-Light-webfont.woff') format('woff'),
         url('/assets/fonts/opensans/opensans_light_macroman/OpenSans-Light-webfont.ttf') format('truetype'),
         url('/assets/fonts/opensans/opensans_light_macroman/OpenSans-Light-webfont.svg#open_sansbold') format('svg');
    font-weight: 300;
    font-style: normal;
}

